// 
let baseURl = '/Project/shopping_mall/static/mock'
let env = process.env.env_config

function doRequest(url){
    let doUrl = `${baseURl}${url}`
    if(env === 'dev' || env === 'prod') {
        return `${doUrl}.json`
    }
    if(env === 'test') {
        return `${doUrl}`
    }

}

// api
let request = {
    url: {
        home: doRequest('/home'),
        classify: doRequest('/classify'),
        city: doRequest('/city')
    }
}

export { request }


